import cuCustom from '@/colorui/components/cu-custom.vue'
import uniCollapse from "@/components/uni-collapse/uni-collapse.vue"
import swiperTabHead from "@/components/tab/swiper-tab-head.vue"
import uniCollapseItem from "@/components/uni-collapse-item/uni-collapse-item.vue"
export default {
	data() {
		return {
			// 轮播图
			cardCur: 0,
			url: '/pages/plaza/plaza',
			swiperList: [{
				id: 0,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84000.jpg'
			}, {
				id: 1,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big37006.jpg',
			}, {
				id: 2,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big39000.jpg'
			}],
			// 折叠面板
			dotStyle: true,
			showAnimation: true,
			open: false,
			name: 'UniCollapseItem',
			animation: "true",
			// tab切换
			tabBars: [{
					name: "动态",
					id: "dongtai"
				},
				{
					name: "社团",
					id: "社团"
				}
			],
			swiperheight: 500, //高度
			tabIndex: 0, // 选中的
			// 动态图标
			lovename: 'cuIcon-likefill',
			infoName: 'cuIcon-messagefill',
			isBottom: false,
			baseUrl: 'http://47.104.29.236:9999/api/v1/',
			userid: '',
			users: [],
			pages: []
		};
	},
	components: {
		cuCustom,
		uniCollapse,
		uniCollapseItem,
		swiperTabHead
	},
	onLoad: function(option) {
		this.userid = option.userid
		this.getSpeak(this.userid)
		// console.log(this.userid)
		uni.request({
			url: this.baseUrl + 'plaza_fill/' + this.userid,
			method: 'GET',
			data: {},
			success: res => {
				// console.log(res)
				this.pages = res.data.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		getSpeak(userid) {
			// console.log(userid)
			uni.request({
				url: this.baseUrl + 'self_info/' + userid,
				method: 'GET',
				data: {},
				success: res => {
					// console.log(res)
					this.users = res.data.data
				},
				fail: () => {},
				complete: () => {}
			});
			uni.request({
				url: this.baseUrl + 'our_tag/' + userid,
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
					// this.users = res.data.data
				},
				fail: () => {},
				complete: () => {}
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		change() {},
		//滑动切换导航
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		//接受子组件传过来的值点击切换导航
		tabtap(index) {
			this.tabIndex = index;
		},
		// 跳转到广场个人
		openpersonal() {
			uni.navigateTo({
				url: '../plaza_personal/plaza_personal'
			});
		},
	}
}
